import Control from "../common/controll";
import {IWorkItem, QuestionsGenerator} from "../QuestionsGenerator/QuestionsGenerator";

export interface IParams {
    mode: string,
    by: string,
    category: string
}

function shuffle(array: IWorkItem[]) {
    let currentIndex = array.length, randomIndex;
    while (currentIndex != 0) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;
        [array[currentIndex], array[randomIndex]] = [
            array[randomIndex], array[currentIndex]];
    }

    return array;
}

export class QuestionItem extends Control {
    public nextQuestion: () => void
    private element: Control<HTMLElement>;
    private correctAnswer: IWorkItem;
    private shuffledArray: IWorkItem[];
    private title: Control<HTMLElement>;

    constructor(parentNode: HTMLElement, index: number, array: IWorkItem[], by: string) {
        super(parentNode);
        this.correctAnswer = array[0]
        this.shuffledArray = shuffle(array)
        this.element = new Control(this.node, 'div', 'questionItem')
        if (by === 'works') {
            this.title = new Control(this.element.node, 'h5', 'questionItem-title',
                `какую из этих картин написал ${this.correctAnswer.author} ?`)
            this.shuffledArray.forEach(p => {
                const but = new Control(this.element.node, 'div', 'questionItem-variant')
                but.node.style.backgroundImage=`url(./assets/img/${p.imageNum}.jpg)`

                but.node.addEventListener('click', (e) => {
                    this.nextQuestion()
                    console.log("CL", p.imageNum, 'Corrext -', this.correctAnswer.imageNum)
                })
            })
        } else if (by === 'painter') {
            //какой художник нарисовал эту картину- выводим 4 художника
            this.title = new Control(this.element.node, 'h5', 'questionItem-title',
                `Какой художник написал эту картину ?`)
            const imgWrapper = new Control(this.element.node, 'div', '')
            imgWrapper.node.style.backgroundImage=`url(./assets/full/${this.correctAnswer.imageNum}full.jpg)`
            this.shuffledArray.forEach(p => {
                const but = new Control(this.element.node, 'div', 'questionItem-variant', p.author)
                but.node.addEventListener('click', (e) => {
                    this.nextQuestion()
                    console.log("CL", p.author, 'Corrext -', this.correctAnswer.author)
                })
            })
        }
    }
}

export class QuestionItems extends Control {
    private currentQuestion: number;
    private questionItem: QuestionItem;
    private questionsGenerator: IWorkItem[][];

    constructor(parentNode: HTMLElement, params: IParams, gameField: GameField) {
        super(parentNode);
        this.currentQuestion = 0
        this.questionsGenerator = new QuestionsGenerator(params).questionsArray

        const createItem = () => {
            if (this.currentQuestion < this.questionsGenerator.length) {
                this.questionItem = new QuestionItem(this.node, this.currentQuestion,
                    this.questionsGenerator[this.currentQuestion], params.by)
                this.currentQuestion++
                this.questionItem.nextQuestion = function () {
                    createItem()
                    this.destroy()
                }
            }
            else {
                gameField.finishClick()
            }
        }
        createItem()

    }
}


export class GameField extends Control {
    public finishClick: () => void;
    private gameFieldWrapper: Control<HTMLElement>;
    private gameFinishGame: Control<HTMLButtonElement>;
    private questionItems: QuestionItems;
    public finish: () => void;

    constructor(parentNode: HTMLElement, params: IParams) {
        super(parentNode);
        this.gameFieldWrapper = new Control(this.node, 'div', 'gameField-wrapper', 'GameField')
         this.questionItems = new QuestionItems(this.gameFieldWrapper.node, params, this)
        this.finish=()=>{
            console.log("JJ")
            this.finishClick()
        }
        // this.gameFinishGame.node.onclick = () => {
        //     this.finishClick()
        // }
    }
}
